<template lang="pug">
    -
        let formList = [
        { label:'供应商名称',name:'name'},
        { label:'统一社会信用代码',name:'creditCode'},
        { label:'法定代表人',name:'legalPerson'},
        { label:'身份证号',name:'identity'},
        { label:'用户名',name:'username'},
        { label:'密码',name:'password'},
        // { label:'确认密码',name:'secondPassword'},
        { label:'业务联系人',name:'businessContact'},
        { label:'业务联系人电话',name:'businessContactPhone'},
        { label:'业务负责人',name:'businessResponsible'},
        { label:'业务负责人电话',name:'businessResponsiblePhone'},
        { label:'应急联系人',name:'emergencyContact'},
        { label:'应急联系人电话',name:'emergencyContactPhone'},
        ]

    -
        let formUploadList = [
        { label: '营业执照扫描件', name:"businessImg" },
        { label: '诚信经营承诺书', name:"commitmentPage" ,link:"/static/downloads/cx.doc"},
        { label: '法人授权书', name:"authorizePage" ,link:"/static/downloads/fd.doc"},
        { label: '业务负责人任命书', name:"appointPage" ,link:"/static/downloads/zr.doc"},
        { label: '设备或系统简介', name:"systemDesc" },
        ]

    .pd20
        .navigate-bar
            router-link(to="/examination") 供应商审核
            i.el-icon-arrow-right
            span 供应商详情

        .form-wrap
            el-form( label-position="right" label-width="160px" :model="form" ref="form")
                el-form-item( label="企业所属"  prop="companyArea")
                    el-select( placeholder="请选择企业所属" style="width:100%" v-model="form.companyArea" disabled)
                        el-option(label="省内" :value="1")
                        el-option(label="省外" :value="0")

                el-form-item( label="对接类型"  prop="contactType")
                    el-select( placeholder="请选择对接类型" style="width:100%" v-model="form.contactType" disabled)
                        //- el-option(label="设备对接" value="1")
                        el-option(label="系统对接" :value="2")
                        el-option(label="软硬件适配（硬件适配人脸识别app）" :value="3")

                each formItem in formList
                    el-form-item(label=formItem.label  prop=formItem.name)
                        el-input( placeholder=`请输入${formItem.label}` v-model=`form.${formItem.name}` disabled)
                    // if formItem.label == '验证码'
                    //     el-input( placeholder=`请输入${formItem.label}` v-model=`form.${formItem.name}` )
                    //     template(slot="append")
                    //         img.codeImg(:src="codeImg" @click="changeImage")

                each uploadItem in formUploadList
                    el-form-item(label=uploadItem.label prop=uploadItem.name empty-message=`请上传${uploadItem.label}` )
                      a.link(:href=`form.${uploadItem.name}` download=`${uploadItem.label}.doc` style="margin-left:15px;cursor:pointer")=`下载${uploadItem.label}`

</template>
<script>
import utils from '@/utils/utils'
export default {
  data () {
    return {
      form: {
        name: ''
      }
    }
  },
  computed: {
    uuid () {
      console.log(this.$route)
      return this.$route.query.uuid
    }
  },
  beforeMount () {
    this.getCompanyInfo(this.uuid)
  },
  methods: {
    getCompanyInfo (uuid) {
      this.$ajax.post('/company/getCompanyByUuid', { uuid })
        .then((res) => {
          if (utils.isEmpty(res.result)) {
            this.$message.error('公司详情列表为空')
            return
          }
          console.log(res.result)
          Object.assign(this.form, res.result)
          // this.form.name = '123'
          // console.log(this.form.name)
        })
        .catch((e) => {
          this.$message.error(e.msg || '获取公司详情失败')
        })
    }
  }
}
</script>
<style lang="scss">
.form-wrap {
  width: 500px;
  .link{
    cursor: pointer;
  }
  .el-input.is-disabled .el-input__inner {
    // background: transparent;
    color: #333;
  }
}
